* {
  box-sizing: border-box;
  // overflow: hidden;
  overflow-y: scroll;
  scrollbar-width: none;
  /* Firefox */
  -ms-overflow-style: none;
}

*::-webkit-scrollbar {
  display: none;
}

.page {
  height: auto;
  min-height: 1000px;

  // margin: auto;
  .header {
    font-family: Verdana, sans-serif;
    //background-color: rgb(151, 97, 11);
    background-color: #17202A;
    width: 100%;
    height: 100vh;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    //padding-top: 100px;
    background-image: url(./../images/header.png);
    background-position: center;
    background-size: cover;

    background-repeat: no-repeat;

    .menu {
      background-color: rgba(18, 18, 19, 0.15);
      -webkit-backdrop-filter: blur(4px);
      backdrop-filter: blur(4px);
      width: 100%;
      height: 50px;
      border-radius: 0px 0px 15px 15px;
      display: flex;
      flex-wrap: wrap;

      .burger {
        display: none;

        .burger_image {
          display: none;
        }
      }

      .name {
        border-radius: 50%;
        // width: 63px;
        // height: 76px;
        margin-left: 24px;
        background-color: #be7d02;

        .inside_name {
          border-radius: 50%;
          text-decoration: none;
          align-items: center;
          width: 50px;
          height: 50px;

          background-image: url('../images/logo.png');
          background-size: cover;
          background-position:top;
          background: cover;
          // aspect-ratio: auto 136 / 48; 
        }
      }

      .write_menu {

        margin: auto;
        align-items: center;
        text-align: center;
        justify-content: space-between;

        .write {
          text-decoration: none;
          padding: 18px;
          color: rgb(255, 255, 255);
          font-size: 20px;

          &:hover {
            height: 50px;
            filter: brightness(120%);
            -webkit-filter: brightness(120%);
            background-color: #be7d02;
          }
        }

      }
    }

    .cover_head {
      padding-top: 200px;
      width: 80%;
      margin: auto;

      .me {
        align-items: center;
        text-align: center;
        justify-content: center;
        margin: auto;

        //font-family: 'Gloria Hallelujah', cursive;
        .title_name {

          margin: auto;
          color: rgb(255, 255, 255);
          font-size: 100px;
          font-weight: bold;
        }

        .title_status {
          color: white;
          font-size: 40px;
        }

      }

      // 
    }
  }

